.about {
  background-color: rgb(17, 17, 17);
  color: #ffffff;
  /* height: 100vh; */
  padding: 20vh 0;
}

.profileContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.profilePicture {
  background-color: white;
  width: 300px;
  height: 400px;
}

.infoContainer {
  display: flex;
  flex-direction: column;
  flex: 1;
  margin-left: 5vw;
}

.aboutTitle {
  flex: 1;
}

.buttonContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-top: 2vh;
}
