.greeting {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

.titleContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  -webkit-user-select: none; /* Chrome all / Safari all */
  -moz-user-select: none; /* Firefox all */
  -ms-user-select: none; /* IE 10+ */
  user-select: none; /* Likely future */
}

.helloTitle {
  font-size: 36px;
}

.greetingTitle {
  font-size: 48px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.typingText {
  overflow: hidden;
  white-space: nowrap;
}

@keyframes typing {
  0% {
    width: 0;
  }
  20% {
    width: 100%;
  }
  50% {
    width: 100%;
  }
  80% {
    width: 100%;
  }
  100% {
    width: 0;
  }
}

.sloganTitle {
  font-size: 36px;
  margin-top: 6vh;
}
